import React, { Component } from 'react';
import axios from 'axios';

class MiniBlog extends Component {

  constructor(props, context) {
    super(props, context);
    this.state = {
      name: '',
      title: '',
      content: '',
      blogPosts: []
    }
  }

  componentDidMount() {
    this.getData()
  }

  getData = () => {
    axios.get(`/api/posts`)
      .then(res => {
        const blogPosts = res.data;
        this.setState({ blogPosts });
      })
  }

  handleSubmit = (event) => {
    event.preventDefault();
    let newData = {
      name: this.state.name,
      title: this.state.title,
      content: this.state.content
    }
    console.log('newData',newData)
    axios.post('/api/posts', newData)
    this.clearForm()
    this.getData()
  }

  handleChange = (event) => {
    let nam = event.target.name;
    let val = event.target.value;
    this.setState({ [nam]: val });
  }

  displayBlogPosts = () => {
    // let { blogPosts } = this.state
    let blogPosts = [
      {
        "_id": "5de6cb5c9404ea29e987130c",
        "name": "misjh",
        "title": "holidays again",
        "content": "turkey",
        "date": "2019-12-03T20:53:48.534Z",
        "__v": 0
      },
      {
        "_id": "5de6c9e705df7ddd75f5bef9",
        "name": "mish",
        "title": "happy holidays",
        "date": "2019-12-03T20:47:35.513Z",
        "__v": 0
      },
      {
        "_id": "5de5b9c9161eda0015dff336",
        "name": "kjqe",
        "title": "test",
        "date": "2019-12-03T01:26:33.538Z",
        "__v": 0
      },
      {
        "_id": "5dd87e6002da6d4391ed842b",
        "name": "eva",
        "title": "kladjfksd",
        "date": "2019-11-23T00:33:36.285Z",
        "__v": 0
      },
      {
        "_id": "5dd87e3857bae3342b8cca2c",
        "name": "Mish",
        "title": "It's friday",
        "date": "2019-11-23T00:32:56.802Z",
        "__v": 0
      }]
    // if(blogPosts.length === 0){
    //   this.setState({blogPosts: [...more]})
    // }
    return blogPosts.map((post) => {
      return <div className="post" key={post._id}>
        <h2>{post.title}</h2>
        <h5>Author: {post.name}</h5>
        <p>{post.content}</p>
      </div>
    })
  }

  clearForm = () => {
    document.querySelector(".createNew").reset();
  }

  render() {
    return (
      <div className="MiniBlog">
        <form className="createNew" onSubmit={this.handleSubmit}>
          <div>
            <label htmlFor='title'>Title</label>
            <input
              type='text'
              name='title'
              placeholder='...title for new blog'
              onChange={this.handleChange}
            />

            <label htmlFor="content">Blog Content</label>
            <input type="textarea" name="content" onChange={this.handleChange} />

            <label htmlFor='name'>Author</label>
            <input
              type='text'
              name='name'
              id='name'
              placeholder='...writtend by'
              onChange={this.handleChange}
            />

            <button>Add A New Blog</button>

          </div>
        </form>

        {this.displayBlogPosts()}
      </div>
    );
  }
}

export default MiniBlog;